/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.8rem;
}

p {
  color: #000;
  font-size: 2rem;
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
  font-family: "Fraunces", serif;
  font-weight: 700;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.header {
  background-image: url("../img/mobile/image-header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 56rem;
}
@media (min-width: 768px) {
  .header {
    background-image: url("../img/desktop/image-header.jpg");
    height: 80vh;
  }
}
@media (min-width: 1024px) {
  .header {
    height: 100vh;
  }
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
  position: relative;
}
.header__navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 0;
  padding: 0;
  overflow: hidden;
  background-color: #FFFFFF;
  position: absolute;
  top: 10rem;
  right: 0;
}
@media (min-width: 1024px) {
  .header__navegacion {
    flex-direction: row;
    align-items: center;
    gap: 3rem;
    position: unset;
    width: auto;
    height: auto;
    background-color: transparent;
  }
}
.header__toggle {
  height: auto;
  padding: 3rem 0;
  overflow: visible;
}
.header__toggle:before {
  content: "";
  width: 0;
  height: 0;
  border-right: 1.5rem solid #FFFFFF;
  border-top: 1.5rem solid transparent;
  border-left: 1.5rem solid transparent;
  border-bottom: 1.5rem solid #FFFFFF;
  position: absolute;
  top: -3rem;
  right: 0;
}
@media (min-width: 1024px) {
  .header__barra {
    display: none;
  }
}
.header__enlace {
  color: hsl(232deg, 10%, 55%);
  margin-bottom: 3rem;
}
@media (min-width: 1024px) {
  .header__enlace {
    color: #FFFFFF;
    margin: 0;
  }
}
.header__boton {
  background-color: hsl(51deg, 100%, 49%);
  padding: 1.5rem 3rem;
  font-family: "Fraunces", serif;
  font-weight: 700;
  text-align: center;
  border-radius: 5rem;
  color: #000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.header__boton:hover {
  cursor: pointer;
  background-color: rgba(250, 212, 0, 0.3);
  color: #FFFFFF;
}
@media (min-width: 1024px) {
  .header__boton {
    background-color: #FFFFFF;
    padding: 1.5rem 3rem;
    font-family: "Fraunces", serif;
    font-weight: 700;
    text-align: center;
    border-radius: 5rem;
    color: #000;
    text-decoration: none;
    transition: background-color 0.3s ease;
    border: none;
    font-size: 1.5rem;
  }
  .header__boton:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.3);
    color: #FFFFFF;
  }
}
.header__heading {
  margin: 8rem 0 6rem 0;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  letter-spacing: 0.5rem;
  user-select: none;
}
@media (min-width: 1024px) {
  .header__heading {
    letter-spacing: 1rem;
    font-size: 5rem;
  }
}
.header__flecha {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .principal {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}
.principal__imagen {
  padding: 18rem 0;
}
.principal__imagen--inicio {
  background-image: url("../img/mobile/image-transform.jpg");
  background-size: cover;
  background-position: center;
}
@media (min-width: 1024px) {
  .principal__imagen--inicio {
    background-image: url("../img/desktop/image-transform.jpg");
  }
}
.principal__imagen--copa {
  background-image: url("../img/mobile/image-stand-out.jpg");
  background-size: cover;
  background-position: center;
}
@media (min-width: 1024px) {
  .principal__imagen--copa {
    background-image: url("../img/desktop/image-stand-out.jpg");
  }
}
.principal__card {
  padding: 8rem 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .principal__card {
    padding: 22%;
    text-align: left;
  }
}
.principal__card--inicio {
  grid-row: 1/2;
}
.principal__card-heading {
  font-size: 3.5rem;
  font-weight: 900;
}
.principal__parrafo {
  color: hsl(232deg, 10%, 55%);
  line-height: 1.6;
}
.principal__parrafo--verde {
  color: hsl(167deg, 40%, 24%);
}
.principal__parrafo--azul {
  color: hsl(198deg, 62%, 26%);
}
.principal__subtitle-amarillo {
  color: #000;
  font-family: "Fraunces", serif;
  font-weight: 700;
  margin-top: 1rem;
  font-size: 2rem;
  position: relative;
}
.principal__subtitle-amarillo:after {
  content: "";
  position: absolute;
  bottom: -0.2rem;
  left: -0.5rem;
  border-radius: 1rem;
  width: 110%;
  height: 1rem;
  background-color: rgba(250, 212, 0, 0.3);
}
.principal__subtitle-rosa {
  color: #000;
  font-family: "Fraunces", serif;
  font-weight: 700;
  margin-top: 1rem;
  font-size: 2rem;
  position: relative;
}
.principal__subtitle-rosa:after {
  content: "";
  position: absolute;
  bottom: -0.2rem;
  left: -0.5rem;
  border-radius: 1rem;
  width: 110%;
  height: 1rem;
  background-color: rgba(254, 120, 103, 0.3);
}
.principal__card-2 {
  padding: 45rem 2rem 5rem 2rem;
  text-align: center;
}
.principal__card-2--verde {
  background-image: url("../img/mobile/image-graphic-design.jpg");
  background-size: cover;
  background-position: center;
  color: hsl(167deg, 40%, 24%);
}
@media (min-width: 1024px) {
  .principal__card-2--verde {
    background-image: url("../img/desktop/image-graphic-design.jpg");
  }
}
.principal__card-2--azul {
  background-image: url("../img/mobile/image-photography.jpg");
  background-size: cover;
  background-position: center;
  color: hsl(198deg, 62%, 26%);
}
@media (min-width: 1024px) {
  .principal__card-2--azul {
    background-image: url("../img/desktop/image-photography.jpg");
  }
}
@media (min-width: 768px) {
  .principal__card-2 {
    padding: 45rem 22% 5rem 22%;
  }
}